/* Popup */

.popup-container {
  font-family: 'Georama';
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  height: 438px;
  width: 268px;
  box-sizing: border-box;
  padding-top: 12px 0 5px 0;
  background: #46474c;
  margin: auto;
  /* border with two colors*/
  border: 3px solid #84be5d;
}


/* Colors */

.popup-container {
  color: rgb(255, 255, 255);
}
.header-title {
  color: #84be5d
}
.link {
  color: white;
}
.btn-text {
  color: #2f3033;
}


/* Header */

.header-container {
  display: flex;
  flex-direction: column;
  height: fit-content;
  width: 100%;
  margin: 0 auto;
  box-sizing: border-box;
}
.header-title {
  font-size: 2.4em;
  font-weight: bold;
  text-align: center;
  text-shadow: 0 1px 2px rgb(11, 41, 46);
}


/* Links */
.links-container {
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 90%;
  margin-right: 7px;
}
.link {
  font-size: 1.12em;
  margin: 0 auto;
  text-decoration: none;
}
.link:hover,
.footer-link:hover {
  text-decoration: underline;
}

