.options {
  font-size: 1.25em;
  height: 100%;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.content {
  margin: 5px auto;
}

.option {
  margin: 7px 0;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.options-title {
  text-align: center;
  font-size: 1.9em;
  text-shadow: 0 1px 2px rgb(11, 41, 46);
}
.option-label {
  font-size: 1.2em;
  word-wrap: break-word;
}
.option-checkbox {
  font-size: 1.25em;
  cursor: pointer;
  margin: 5px 8px;
  display: inline-block;
}
#status {
  font-size: .9rem;
  margin: auto;
  text-align: center;
  opacity: .6;
  margin-top: 4px;
}
.save-btn {
  background: rgb(255, 255, 255, .9);
  border: 0;
  border-radius: 5px;
  padding: 6px 32px;
  font-size: 1.4em;
  font-weight: bold;
  margin: 18px auto 6px auto; 
  display: block;
  cursor: pointer;
}

input[type=checkbox] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  
  width: 15px;
  height: 15px;
  padding: 0px;

  background-clip: content-box;
  border: 1.5px solid #bbbbbb;
  background-color: #f1eff1;
  margin-right: 5px;
}
input[type=checkbox]:checked {
    background-color: #84be5d;
}

input[type=checkbox]:focus {
    outline: none !important;
}

